import React, { useState } from 'react';
import './App.css';

function App() {
  let [count, setCount] = useState( () => { return 4} )

  function decrementCount() {
    setCount(prevCount => prevCount - 2)
  }

  function incrementCount() {
    setCount(prevCount => prevCount + 2)
    //setCount(prevCount => prevCount + 2)
  }

  return (
    <>
    <div className="App">
      <button onClick={decrementCount} className="mbutton">-</button>
      <span>{count}</span>
      <button onClick={incrementCount} className="pbutton">+</button>
    </div>
    </>
  );
}

export default App;
